.intro-wrapper {
    padding-top: 48px;
    padding-bottom: 48px;
    padding-left: 128px;
    padding-right: 128px;
    display: flex;
}

.intro-left-side {
    flex: 30%;
    margin: auto;
}

.intro-right-side {
    flex: 70%;
    padding-left: 64px;
    justify-content: center;
    margin: auto;
}

.greeting {
    text-align: center;
    font-size: 64px;
    padding-bottom: 16px;
}

.intro-paragraph {
    font-size: 32px;
    padding-bottom: 32px;
}

.intro-image-row {
    display: flex;
    align-items: center;
}

.column {
    flex: 50%;
    padding: 5px;
}

.intro-image-item {
    width: 100%;
}



@media only screen and (min-width: 1000px) and (max-width: 1600px) {
    .intro-wrapper {
        padding-top: 48px;
        padding-bottom: 48px;
        padding-left: 128px;
        padding-right: 128px;
        display: flex;
    }

    .intro-image-row {
        display: block;
    }
    
    .column {
        flex: 50%;
        padding: 5px;
    }

    .intro-image-item {
        width: 100%;
    }
    
  }

@media only screen and (max-width: 999px) {
    .intro-wrapper {
        display: block;
        padding: 8px;
        width: 100%;
    }

    .intro-image-row {
        display: inline-flex;
        ;
    }
    
    .column {
        flex: 50%;
        padding: 5px;
    }

    .intro-image-item {
        width: 200px;
    }
    
    .intro-right-side {
        flex: none;
        padding: 8px;
        margin: auto;
    }

    .intro-paragraph {
        font-size: 24px;
        text-align: center;
    }

    .intro-left-side {
        display: block;
        justify-content: center;
        text-align: center;
    }
}