video {
    object-fit: cover;
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: -1;
}

.hero-container {
    background: url('../assets/images/smoky-mountains.jpg') center center/cover no-repeat;
    height: 100%;
    width: 100%;
    display: flex;
    color: white;
    margin: auto;
    flex-direction: column;
    align-items: center;
    box-shadow: inset 0 0 0 1000px rgba(0,0,0,0.2);
    object-fit: contain;
}

.header {
    height: 100vh;
    width: 100%;
    margin-top: 20px;
    color: white;
    align-items: center;
    text-align: center;
    object-fit: contain;
    display: block;
}

.header h1 {
    font-size: 72px;
}

.header h2 {
    font-size: 56px;
    padding: 8px;
}

.image {
    margin-top: 8px;
    width: 300px;
}

@media only screen and (max-width: 999px) {
    .header h1 {
        font-size: 56px;
    }
    
    .header h2 {
        font-size: 32px;
        padding: 8px;
    }
    
    .image {
        margin-top: 8px;
        width: 300px;
        height: auto;
    }

    .header {
        display: block;
        justify-content: center;
        text-align: center;
    }
}

@media only screen and (min-width: 1000px) and (max-width: 1600px) {

    .header h1 {
        font-size: 56px;
    }
    
    .header h2 {
        font-size: 36px;
        padding: 8px;
    }

    .image {
        width: 300px;
        
    }
    
  }