.projects-header {
    font-size: 72px;
    padding-top: 16px
}

.project-container {
    display: flex;
    padding: 128px;
    padding-bottom: 64px;
    clear: both;
    justify-content: center;
    text-align: center;
}

.project-left-side {
    flex: 25%;
    margin: auto;

}

.project-right-side {
    flex: 75%;
    margin: auto;
    padding-left: 128px;
    text-align: left;
}

.project-title {
    font-size: 36px;
    text-align: center;
    padding-bottom: 8px;
}

.project-img {
    width: 300px;
    height: auto;
    object-fit: cover;
    transition: all 0.2s linear;
    padding-left: 0px;
    padding-bottom: 0px;
}

/* .project-img-style {
    display: inline-block;
    vertical-align: top;
    clear: both;
    padding: 16px;
} */

.project-text {
    font-size: 36px;
    margin-bottom: 16px;
}

.projects-list {
    margin: 32px;
}



@media only screen and (max-width: 999px) {
    .project-img {
        width: 256px;
        height: auto;
        object-fit: cover;
        transition: all 0.2s linear;
    }

    .project-text {
        font-size: 24px;
    }

    .project-container {
        display: block;
        padding: 16px;
        padding-bottom: 8px;
    }

    .project-left-side {
        flex: none;
    }
    
    .project-right-side {
        flex: none;
        padding-left: 0px;
        padding-top: 16px;
        text-align: center;
    }
  }

@media only screen and (min-width: 1000px) and (max-width: 1600px) {
    .project-img {
        width: 256px;
        height: auto;
        object-fit: cover;
        transition: all 0.2s linear;
        
    }

    .project-text {
        font-size: 32px;
    }

    .project-container {
        padding: 16px;
        padding-bottom: 8px;
    }

    .project-left-side {

    }
    
    .project-right-side {
        padding-left: 32px;
        padding-top: 16px;
    }
  }

